import {api} from "../components/api/api";

const SET_BEST_CHANGE_DATA = "myApp/bestChangeReducer/SET_BEST_CHANGE_DATA"; //    

type setBestChangeDataActionType = {type: typeof SET_BEST_CHANGE_DATA, bestChangeData:any}
export let setBestChangeData = (bestChangeData:any):setBestChangeDataActionType => { //   
  return {type: SET_BEST_CHANGE_DATA, bestChangeData }
};

type initialStateType = {
  bestChangeData:any
}
let initialState:initialStateType = { //   
  bestChangeData: null, //   bll  
}

let bestChangeReducer = (state:initialStateType = initialState, action:any):initialStateType => {//  
  let stateCopy:initialStateType; //      
  switch (action.type) {
    case SET_BEST_CHANGE_DATA: //       
      stateCopy = {
        ...state, //   
        bestChangeData: action.bestChangeData, //    BLL
      }
      return stateCopy; //     
    default:
      return state; //     
  }
}

export let getBestChangeDataTC = () => {//     
  return async (dispatch: any) => { //      
    const response1 = await api.getBestChangeData()  //    
    if (response1) {
      console.log(response1)
      dispatch(setBestChangeData(response1))  //   localStorage    store
    }

  }
}


export default bestChangeReducer;










